@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@300&family=Josefin+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom right, rgb(255, 120, 120), rgb(255, 255, 120)) no-repeat center center fixed;
  background-size: cover;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.locations {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
  
.location {
    font-family: 'Josefin Sans', sans-serif;
    flex-basis: 28%;
    margin: 1em;
    padding: 1em;
    border-radius: 15px;
    border: 3px solid green;
    background-color: white;
    min-width: 300px;
    max-width: 300px;
}

.locationContainer {
    text-align: center;
}
.fetchError {
    background-color: rgb(255, 77, 77);
    border-radius: 5px;
}

.fetchError h3 {
    color: white;
    margin: 5px;
}
.products {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
  
.product {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 1em;
    border: 2px solid rgb(103, 103, 255);
    border-radius: 15px;
    background: white;
    width: 470px;
    height: 280px;
    overflow: hidden;
}

.productData {
    font-family: 'Bitter', serif;
    display: flex;
    flex-direction: column;
    width: 200px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 5px;
}

.product__location {
    display: flex;
    flex-direction: column;
    margin: 1em;
    padding: 4px;
    border: 1px solid rgb(179, 116, 0);
    border-radius: 10px;
}

.product__location p {
    margin: 0px;
}

.product__location h5 {
    margin-top: 0px;
    margin-bottom: 5px;
}

.productContainer {
    text-align: center;
}

.productImage img {
    padding: 1em;
    object-fit: contain;
    width: 90%;
    height: 90%;
}

.productImage {
    height: 100%;
    width: 60%;
}


.header h1 {
    font-family: 'Satisfy', cursive;
    text-align: center;
    font-size:xx-large;
}

.header {
    padding: 10px;
    background-color: coral;
    border: 2px solid black;
}
